import React, { useState, useEffect } from "react";
import { Button, Tabs, Tab, Box, Anchor, ResponsiveContext } from "grommet";
import CardListing from "../../components/CardList";
import { Cart, ShareOption } from "grommet-icons";
import CRUDForm from "../CRUDForm";
import ProductFormConfig from "./ProductFormConfig";
import Modal from "../../components/Modal";
import { crud, searchProduct, search } from "../../library/parse";
import EventObserver from "../EventObserver";
import SellingMode from "./SellingMode";
import OrderListing from "../Order/OrderListing";
import ProductUploader from "./ProductUploader";
import { getPages } from "../../library/FB";
import UserContext from "../../context/UserContext";
import Share from "../Share";
const Component = ({ storeId, editMode, onClickAppsIcon }) => {
  const _user = React.useContext(UserContext);
  const size = React.useContext(ResponsiveContext);
  const [uploadMultipleMode, setUploadMultipleMode] = useState(false);
  const [uploadSingleProductMode, setUploadSingleProductMode] = useState(false);
  const [cudMode, setCUDMode] = useState("");
  const [activeModel, setActiveModel] = useState({});
  const [showActionModal, setShowActionModal] = useState(false);
  const [showShareModal, setShareActionModal] = useState(false);
  const [event, setEvent] = useState(null);
  const [
    scheduledForFetchingProduct,
    setScheduledForFetchingProduct,
  ] = useState(false);
  const [products, setProducts] = useState([]);
  const [pages, setPages] = useState([]);
  const scheduleAction = (sec, action) => {
    if (!scheduledForFetchingProduct) {
      setTimeout(() => {
        action && action();
        setScheduledForFetchingProduct(false);
      }, sec);
      setScheduledForFetchingProduct(true);
    }
  };
  const loadProducts = () => {
    searchProduct({
      storeId: {
        f: "equalTo",
        v: storeId,
      },
      limit: 100,
      descending: "createdAt",
    }).then((e) => setProducts(e));
  };

  useEffect(() => {
    loadProducts(storeId);
  }, [storeId, event]);

  return (
    <Box>
      {editMode && (
        <Box fill={true} gap="small" direction="column">
          <Box>
          <Anchor onClick={() => setUploadSingleProductMode(true)}> Upload Single Product</Anchor>
          </Box>
          <Box>
          <Anchor onClick={() => setUploadMultipleMode(true)}>
            Upload Multiple Product
          </Anchor>
          </Box>
        </Box>
      )}
      <CardListing
        isOwner={editMode}
        showFallBack={true}
        onClickAppsIcon={() => setShowActionModal(true)}
        dataList={products.map((e) => {
          const acl = e.getACL();
          const hideButtonText = acl.getPublicReadAccess() ? "Hide" : "Show";

          return {
            title: e.get("name"),
            thumbnail: e.get("photos"),
            cta: (
              <>
                {pages.map((e) => {
                  return <>{e.access_token}</>;
                })}
                {
                  "" && <>
                <SellingMode product={e} storeId={storeId} />
                {editMode && (
                  <>
                    <Box direction="row">
                      <Button
                        onClick={() => {
                          setActiveModel(e.toJSON());
                          setCUDMode("update");
                        }}
                        size="small"
                        margin="small"
                        primary
                        label="Edit"
                      />
                      <Button
                        color={
                          hideButtonText === "Show" ? "status-unknown" : false
                        }
                        onClick={() => {
                          setActiveModel(e.toJSON());
                          if (hideButtonText === "Show") {
                            setCUDMode("show");
                          } else {
                            setCUDMode("hide");
                          }
                        }}
                        size="small"
                        margin="small"
                        primary
                        label={hideButtonText}
                      />
                    </Box>
                    <Box direction="row">
                      <Button
                        onClick={() => {
                          setActiveModel(e.toJSON());
                          setCUDMode("delete");
                        }}
                        size="small"
                        margin="small"
                        primary
                        label="Delete"
                      />
                                            <Button
                        onClick={() => {
                          setActiveModel(e.toJSON());
                          setShareActionModal(true)
                        }}
                        size="small"
                        margin="small"
                        primary
                        label="Share"
                      />
                    </Box>
                    <Box direction="row">

                    </Box>
                  </>
                )}
                </>
                }
                                    <Box direction="row">
                      <Button
                        onClick={() => {
                          setActiveModel(e.toJSON());
                          setCUDMode("delete");
                        }}
                        size="small"
                        margin="small"
                        primary
                        label="Delete"
                      />
                                            <Button
                        onClick={() => {
                          setActiveModel(e.toJSON());
                          setShareActionModal(true)
                        }}
                        size="small"
                        margin="small"
                        primary
                        label="Post on Facebook"
                      />
                    </Box>
              </>
            ),
          };
        })}
      />
      {(uploadMultipleMode || uploadSingleProductMode) && (
        <Modal
          full={size === "small"}
          onClose={() => {
            setUploadMultipleMode(false)
            setUploadSingleProductMode(false)
          }}
        >
          <ProductUploader singleProductMode={uploadSingleProductMode} storeId={storeId} />
        </Modal>
      )}
      {cudMode && (
        <Modal full={size === "small"} onClose={() => setCUDMode("")}>
          <Tabs>
            <Tab title="Information">
              <CRUDForm
                onClick={(action, model) => {
                  return crud(
                    "Product",
                    {
                      ...model,
                      available: true,
                    },
                    action,
                    "",
                    {
                      storeId: storeId,
                    }
                  ).then((e) => {
                    setCUDMode("");
                    scheduleAction(0, () => loadProducts());
                  });
                }}
                mode={cudMode}
                model={activeModel}
                formConfig={ProductFormConfig}
                objectClass="Product"
              />
            </Tab>
            <Tab title={<Cart />}>
              <OrderListing product={activeModel} />
            </Tab>
            <Tab title={<ShareOption />}>
              <Share 
                model={activeModel} 
                onDone={(id) => {
                  setCUDMode("");
                }}
              />
            </Tab>
          </Tabs>
        </Modal>
      )}
      <EventObserver
        objectClass={"Product"}
        onEvent={(e, action) => {
          scheduleAction(5000, () => setEvent(e));
        }}
      />
      {showShareModal && <Modal onClose={() => setShareActionModal(false)}>
        <Share 
                model={activeModel} 
                onDone={(id) => {
                  
                  setCUDMode("");
                }}
              />
        </Modal>}
    </Box>
  );
};

export default Component;
