import React, { useState, useEffect } from "react";
import { createCart, getProductCartStatus } from "../../library/parse";
import NumberTextInput from "../CRUDForm/NumberTextInput";
import Button from "../../components/Button";
import Parse from "../../library/parse";
import { Box } from "grommet";
import { StatusInfo, Chat } from "grommet-icons";
import Modal from "../../components/Modal";
import { parseISO, format } from "date-fns";
import ChatContext from "../../context/ChatContext";
import UserContext from "../../context/UserContext";
import { useTranslation } from 'react-i18next';

const Component = ({ product, storeId, enquiryComponent }) => {
  
  const { t, i18n } = useTranslation();
  const _userContext = React.useContext(UserContext);
  const [user, setUser] = useState(Parse.User.current());
  const [displayPrice, setDisplayPrice] = useState(
    product.get("startingPrice")
  );
  const [displayCartModal, setDisplayCartModal] = useState(false);
  const [bidAmount, setBidAmount] = useState(1);
  const [productStatus, setProductStatus] = useState([]);
  const [cartStatus, setCartStatus] = useState([]);
  const loadProductCart = async () => {
    const carts = await getProductCartStatus(product);
    setCartStatus(carts);
    carts && carts[0] && setDisplayPrice(carts[0].get("bid"));
  };

  useEffect(() => {
    loadProductCart();
  }, []);

  useEffect(() => {}, [user]);

  return (
    <>
      <Box gap="xsmall" margin="0" direction="row" align="center">
        <ChatContext.Consumer>
          {({ openChat }) => (
            <Box
              onClick={() => {
                if (user) {
                  openChat(`User-${user.id}`, `Store-${storeId}`);
                }
              }}
            >
              <Chat style={{ cursor: "pointer" }} />
            </Box>
          )}
        </ChatContext.Consumer>

        {`Current: $${displayPrice}`}
        <Box
          onClick={() => setDisplayCartModal(true)}
          style={{ cursor: "pointer" }}
        >
          <StatusInfo />
        </Box>
      </Box>
      {_userContext && (
        <Box
          pad="small"
          justify="center"
          alignContent="center"
          direction="row"
          gap="small"
        >
          <Box width={{ max: "40%" }}>
            <NumberTextInput
              value={bidAmount}
              size="small"
              onChange={(v) => setBidAmount(v)}
            />
          </Box>
          <Button
            focusIndicator
            onClick={() => {
              if (user) {
                return createCart(product.id, bidAmount, 1).then(() =>
                  loadProductCart()
                );
              }
            }}
            label={productStatus.length ? "Awarded" : t('MakeOffer')}
            disabled={productStatus.length}
            size="small"
            primary
          />
        </Box>
      )}
      {!_userContext && <>Please Login to Bid</>}
      {displayCartModal && (
        <Modal onClose={() => setDisplayCartModal(false)}>
          <Box gap="small">
            {cartStatus.length === 0 && <Box>No one has made a bid yet</Box>}

            {cartStatus.map((c) => {
              const roleProfile = c.get("rolePublicProfile");
              const custName = roleProfile && roleProfile.get("name");
              const bidAmount = c.get("bid");
              const bidTime = format(c.get("createdAt"), "MMM d HH:mm");
              return (
                <Box>
                  {" "}
                  {`${custName || "user"} bid $${bidAmount} at ${bidTime}`}
                </Box>
              );
            })}
          </Box>
        </Modal>
      )}
    </>
  );
};

export default Component;
