/* global FB */

export const getPages = async (cb, accessToken) => {
  return FB.api(
    "/me/accounts",
    (response) => {
      cb && cb(response);
    },
    {
      access_token: accessToken,
    }
  );
};

const fbapi = (path, method, props) => {
  return new Promise((resolve, reject) => {
    FB.api(
      path,
      method,
      {},
      (response) => {
        resolve(response);
      },
      props
    );
  })
}

export const updatePost = async (pageid, post_id, accessToken, message, props = {}) => {
  return fbapi(`/${pageid}/feed/`, "POST", {
    access_token: accessToken,
    message,
    ...props
  })
};

export const publishPhoto = async (pageid, photoUrl, accessToken, props = {}) => {
  return fbapi(`/${pageid}/photos`, "POST",       {
    url: photoUrl,
    access_token: accessToken,
    ...props
  })
};
