import React, { useState, useEffect } from "react";
import { Adsense } from "@ctrl/react-adsense";
import { Anchor, Box, Select, TextArea } from "grommet";
import Button from '../../components/Button'
import { getPages, publishPhoto, updatePost } from "../../library/FB";
import UserContext from "../../context/UserContext";
import { flattenObject, replaceBracesWith } from '../../helpers/'

const Component = ({ model, onDone }) => {
  const _user = React.useContext(UserContext);
  const [templateConfig, setTemplateConfig] = useState("")
  const [options, setOptions] = useState([]);
  useEffect(() => {
    
    if (_user) {
      const template = _user && _user.config && _user.config.templateConfig || ""
      setTemplateConfig(replaceBracesWith(template, flattenObject(model)))
      const { user } = _user;
      const token =
        user.get("authData") && user.get("authData").facebook.access_token;

      token &&
        getPages((response) => {
          setOptions(
            response.data.map((e) => ({
              label: e.name,
              accessToken: e.access_token,
              value: e.id,
            }))
          );
        }, token);
    }
  }, [_user]);
  return (
    <Box>
      <Select
        value={options && options[0] ? options : false}
        options={options}
        valueKey="value"
        labelKey="label"
      />
      <TextArea 
        height="700px"
        value={templateConfig} 
        onChange={(event) => {
          setTemplateConfig(event.target.value)
        }}
      />
      <Anchor href="/config">Config</Anchor>
      <Box direction="row" gap="small">
      <Button
        label="Share"
        onClick={async () => {
          const page_id = options[0].value;
          const accessToken = options[0].accessToken;
          const photoIds = await Promise.all(model.photos.map(async photo => {
            const { id } = await publishPhoto(page_id, photo.url, accessToken, { published: false })
            return id;
          }))
          const media = photoIds.map(id => ({
            ["media_fbid"]: id
          }))
          return updatePost(page_id, "", accessToken, templateConfig, {
            ["attached_media"]: media
          }).then(e => {
            onDone && onDone(e)
          })
        }}
      />
      </Box>
    </Box>
  );
};

export default Component;
