import React, { useEffect, useState } from "react";
import {
  Heading,
  Accordion,
  AccordionPanel,
  Box,
  Button,
  ResponsiveContext,
  Tabs,
  Tab,
} from "grommet";
import Chatroom from "./Chatroom";
import ReactHtmlParser from "react-html-parser";
import LiveStreamCommerce from "../containers/LiveStreamCommerce";
import ProductListing from "../containers/ProductListing";
import Parse, { getMyRoles, searchFirstStore } from "../library/parse";
import WAButton from "../components/WhatsappButton";
import ChatButton from "../components/Button/Chat";
import Order from "./Order";

const getUserRole = (roles) => roles.find((e) => e.includes("User"));

const Home = ({ location, pathContext, pageContext, uniqueName, ...props }) => {
  const [storeId, setStoreId] = useState(null);
  const size = React.useContext(ResponsiveContext);
  const [enableLiveStream, setEnableLiveStream] = useState(false);
  const [roles, setRoles] = useState([]);
  const [store, setStore] = useState(null);
  const [currentUser, setCurrentUser] = useState(Parse.User.current());

  const [activeAccordionPanel, setActiveAccordionPanel] = useState(0);
  const storeName = store && store.get("name");
  const storeInformation =
    store && ReactHtmlParser(`<div>${store.get("description")}`);
  const isStoreOwner = roles.includes(`Store-${storeId}`);

  const ChatButtons = ({ text, iconOnly }) => (
    <>
      {store && store.get("whatsapp") && (
        <WAButton
          number={store.get("whatsapp")}
          text={text}
          iconOnly={iconOnly}
        />
      )}
      {currentUser && (
        <ChatButton
          iconOnly={iconOnly}
          text={text}
          senderRoleName={getUserRole(roles)}
          recipientRoleName={`Store-${storeId}`}
        />
      )}
    </>
  );

  useEffect(() => {
    currentUser && getMyRoles().then((e) => setRoles(e));
  }, [storeId]);

  useEffect(() => {
    searchFirstStore({
      uniqueName: {
        f: "equalTo",
        v: uniqueName,
      },
    }).then((e) => {
      if (e) {
        setStoreId(e.id);
        setStore(e);
        setEnableLiveStream(e.get("allowLive"));
      }
    });
  }, [uniqueName]);
  return (
    <>
      <Heading level={3}> {storeName} </Heading>
      {
        "" && <Accordion
        onActive={setActiveAccordionPanel}
        activeIndex={activeAccordionPanel}
      >
        <AccordionPanel label="More Information">
          <Box pad="medium" background="light-2">
            <Tabs>
              <Tab title="Information">
                {!!storeInformation && storeInformation}
                <Box margin="xsmall" direction="row" gap="small">
                  <ChatButtons />
                </Box>
              </Tab>
              <Tab title="Orders">
                <Order storeId={storeId} />
              </Tab>
            </Tabs>
          </Box>
        </AccordionPanel>
      </Accordion>
      }
      <br />
      <Box gap="small" direction={size === "small" ? "column" : "row"}>
        <Box>
          {enableLiveStream && (
            <Box width={size === "small" ? "100%" : "720px"} height="405px">
              <LiveStreamCommerce />
            </Box>
          )}
        </Box>
        {enableLiveStream && (
          <Box height="405px" width={size === "small" ? "column" : "100%"}>
            <Chatroom
              senderRoleName={
                currentUser
                  ? isStoreOwner
                    ? `Store-${storeId}`
                    : `User-${currentUser.id}`
                  : false
              }
              recipientRoleName={`Store-${storeId}`}
              publicRoom={true}
              placeholder="talk to everyone in the room"
            />
          </Box>
        )}
      </Box>

      <ProductListing
        storeId={storeId}
        editMode={isStoreOwner}
        ChatButtons={ChatButtons}
      />
    </>
  );
};

export default Home;
