import React from "react";
import { Anchor, AccordionPanel, Accordion, Box, Text } from "grommet";
const Component = ({ actionText, headerLabel, detail, data }) => {
  return (
    <Box>
      {data &&
        data.map((d) => (
          <Accordion>
            <AccordionPanel label={d.header}>
              <Box pad="medium" background="light-2">
                <Box>{d.detail}</Box>
                <Box>
                  <Anchor href="#">{d.actionText}</Anchor>
                </Box>
              </Box>
            </AccordionPanel>
          </Accordion>
        ))}
    </Box>
  );
};

export default Component;
